@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1p&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Chettan+2&family=Baumans&family=Goldman&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{height:100%;margin:0}body{background:#ecc6a2;min-height:100vh;overflow:hidden}.App{text-align:center;position:relative;min-height:100vh}.player{min-height:100%}footer{font-family:"Baloo Chettan 2", cursive;text-align:center;position:fixed;left:50%;bottom:20px}button{position:relative;font-family:"Goldman", cursive;background:#ce5e82;color:#200d3a;border:none;width:80px;height:80px;border-radius:50%;text-transform:uppercase;cursor:pointer;box-shadow:-2px -2px 6px #fff,-2px -2px 8px rgba(255,255,255,0.8),-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}button:hover{background:rgba(206,94,130,0.5);color:#200d3a;box-shadow:inset -2px -2px 6px #fff,inset 2px 2px 4px rgba(255,255,255,0.4),inset 2px 2px 2px rgba(255,255,255,0.2),inset 2px 2px 6px rgba(0,0,0,0.15)}button::after{font-size:1.3rem;content:'► / ❙❙';color:#292651}.playlist{text-align:center;border-top:1px solid rgba(0,0,0,0.2)}.playlist p{font-family:"Poppins", sans-serif;color:#200d3a;font-size:2em}.songs{text-align:left;left:50%;padding:0 30vh 0 30vh}.playlist-item{font-family:"M PLUS 1p", sans-serif;color:#200d3a;border-left:1px solid rgba(0,0,0,0.2);cursor:pointer;padding:1em}.playlist-item:hover{background:rgba(0,0,0,0.1)}.playlist-itemSelected{font-family:"M PLUS 1p", sans-serif;color:#200d3a;border-left:5px solid #ce0505;background:rgba(0,0,0,0.1);cursor:pointer;padding:1em}#waveform{position:relative;padding:0;margin:3em 3em 0 3em}#timeline{margin-left:3em;padding:0}#volume{padding-left:2em}.ControlsBody{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;margin:0;padding:2em 0 2em 0}input[type="range"]{width:300px;margin:10.8px 0;background-color:transparent;-webkit-appearance:none}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-runnable-track{background:#eca089;border:0.2px solid #ffffff;border-radius:2px;width:300px;height:6px;cursor:pointer;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]::-webkit-slider-thumb{margin-top:-11px;width:10px;height:30px;background:#292651;border:0.1px solid #ffffff;border-radius:2px;cursor:pointer;-webkit-appearance:none;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]:focus::-webkit-slider-runnable-track{background:#f0b29f;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]::-moz-range-track{background:#eca089;border:0.2px solid #ffffff;border-radius:2px;width:300px;height:6px;cursor:pointer;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]::-moz-range-thumb{width:10px;height:30px;background:#292651;border:0.1px solid #ffffff;border-radius:2px;cursor:pointer;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]::-ms-track{background:transparent;border-color:transparent;border-width:11.8px 0;color:transparent;width:300px;height:6px;cursor:pointer;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]::-ms-fill-lower{background:#e88e73;border:0.2px solid #ffffff;border-radius:4px;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]::-ms-fill-upper{background:#eca089;border:0.2px solid #ffffff;border-radius:4px;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]::-ms-thumb{width:10px;height:30px;background:#292651;border:0.1px solid #ffffff;border-radius:2px;cursor:pointer;margin-top:0px;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}input[type="range"]:focus::-ms-fill-lower{background:#eca089}input[type="range"]:focus::-ms-fill-upper{background:#f0b29f;box-shadow:-2px -2px 12px rgba(255,255,255,0.6),inset 2px 2px 4px rgba(255,255,255,0.4),2px 2px 2px rgba(255,255,255,0.2),2px 2px 8px rgba(0,0,0,0.15)}@supports (-ms-ime-align: auto){input[type="range"]{margin:0}}

